<template>
    <div class="register-container">
        <!--  -->
        <div class="register">
            <h3
                >
                <span class="go"> <a></a> </span>
            </h3>
            <div class="content">
                <label>:</label>
                <input type="text" placeholder="" v-model="phoneNum" />
            </div>
            <div class="content">
                <label>:</label>
                <input type="text" placeholder="" :value="codeNum" />
                <button @click="re_code"></button>
            </div>
            <div class="content">
                <label>:</label>
                <input type="text" placeholder="" v-model="password" />
            </div>
            <div class="content">
                <label>:</label>
                <input type="text" placeholder="" v-model="passwordCheck" />
                <span class="error-msg">{{ checkTip }}</span>
            </div>
            <div class="controls">
                <input name="m1" type="checkbox" />
                <span></span>
            </div>
            <div class="btn">
                <button @click="register"></button>
            </div>
        </div>

        <!--  -->
        <div class="copyright">
            <ul>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
            <div class="address">6</div>
            <div class="beian">ICP19006430 </div>
        </div>
    </div>
</template>

<script>

export default {
    name: "Register",
    data() {
        return {
            phoneNum: null,
            codeNum: null,
            password: null,
            passwordCheck: null,
        };
    },
    methods: {
        re_code() {
            this.$api.reCode(this.phoneNum)
                .then(res => {
                    this.codeNum = res.data.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },

        register() {
            if (!this.phoneNum || !this.codeNum || !this.password || !this.passwordCheck) {
                alert("");
                return;
            }
            if (this.checkTip) {
                alert("");
                return;
            }
            let params = {
                phone: this.phoneNum,
                password: this.password,
                code: this.codeNum,
            };
            this.$api.reRegister(params)
                .then(res => {
                    if(res.data.ok){
                        alert("");
                        this.$router.push("home");
                    } 
                    else alert(res.data.message)
                })
                .catch(err => {
                    console.log(err);
                });
        },
    },
    computed: {
        checkTip() {
            if (this.password && this.passwordCheck) {
                if (this.password == this.passwordCheck) return null;
                else return "";
            }
            return null;
        },
    },
};
</script>

<style lang="less" scoped>
